* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Archivo Black", sans-serif;
  font-family: "Open Sans", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
}

header {
  height: 100vh;
  width: 80%;
  justify-content: center;
  align-items: center;
}

.main-hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero-left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.hero-right img {
  width: 500px;
  height: 500px;
  border-radius: 70% 30% 70% 30%;
}

.hero-left h1 {
  font-family: "Archivo Black", sans-serif;
  font-size: 57px;
}
